<template>
  <div class="hbhistory">
      <header>
          <router-link to="/benefit"><i class="el-icon-arrow-left" id="arrowleft"></i> </router-link>
          <div class="top_title"><span>历史红包</span></div>
      </header>
      <section class="scroll_container">
          <ul class="hongbao_list_ul">
              <li class="hongbao_list_li" v-for="h in hbArr" :key="h.id">
                  <section class="list_item">
                      <div class="list_item_left">
                          <span>¥</span>
                          <span>{{h.amount}}</span>
                          <span>.</span>
                          <span>0</span>
                          <p>{{h.description_map.sum_condition}}</p>
                      </div>
                      <div class="list_item_right">
                          <h4>{{h.name}}</h4>
                          <p>{{h.end_date}}到期</p>
                          <p>{{h.description_map.phone}}</p>
                      </div>
                  </section>
                  <div class="list_item_footer">
                          <p>{{h.hasOwnProperty("limit_map") ? h.limit_map.restaurant_flavor_ids : ""}}</p>
                  </div>
                  <!--<i class="expired el-icon-error"></i>-->
              </li>
          </ul>
      </section>
  </div>
</template>

<script>
export default {
name:'Hbhistory',
data(){
            return{
                userid:'',
                hbArr:[]
            }
        },
        created(){
            this.userid = localStorage.getItem('userId');
            console.log(this.userid);
            this.axios.get("/promotion/v2/users/"+this.userid+"/expired_hongbaos", {
                params: { user_id: this.userid,limit:10 }
                }).then(res => {
                this.hbArr = res;
                 console.log(res)
                }).catch(err => {});
        }
}
</script>

<style>
.hbhistory{
    overflow: hidden;
}
header{
    background-color: #3190e8;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 1.95rem;
}
a{
    text-decoration: none;
}
header>a #arrowleft{
    left: .4rem;
    width: .6rem;
    height: 1rem;
    color: #fff;
    line-height: 2.2rem;
    margin-left: .4rem;
}
.top_title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
}
.top_title>span{
    font-size: .8rem;
    color: #fff;
    text-align: center;
    font-weight: 700;
}
.hongbao_list_ul{
    padding: 1rem .5rem;
    margin-top: 1.95rem;
}
.hongbao_list_ul .hongbao_list_li{
    background-size: .5rem .2rem;
    background-color: #fff;
    margin-bottom: .5rem;
    border-radius: .25rem;
    position: relative;
}
.hongbao_list_li .list_item{
    display: flex;
    justify-content: space-between;
    padding: 1rem .5rem .8rem;
}
.hongbao_list_li .list_item .list_item_left{
    font-size: 0;
    border-right: .025rem dotted #ccc;
    flex: 1;
}
.list_item_left span:first-of-type {
    font-size: .75rem;
    color: #ccc;
    font-weight: 700;
}
.list_item_left span:nth-of-type(2) {
    font-size: 2rem;
    color: #ccc;
}
.hongbao_list_ul .hongbao_list_li .list_item .list_item_left span:nth-of-type(3), .hongbao_list_ul .hongbao_list_li .list_item .list_item_left span:nth-of-type(4) {
    font-size: 1rem;
    color: #ccc;
    font-weight: 700;
}
.list_item_left p{
    font-size: .4rem;
    color: #999;
}
.list_item_right{
    flex: 2;
    margin-left: 1.5rem;
}
.list_item_right h4{
    font-size: .7rem;
    color: #666;
    margin-left: -.7rem;
}
.list_item_right p{
    list-style-type: disc;
    margin-left: -.7rem;
    font-size: .4rem;
    color: #999;
}
.list_item_footer{
    background-color: #f9f9f9;
    padding: .4rem;
    border-bottom-left-radius: .25rem;
    border-bottom-right-radius: .25rem;
}
.expired{
    display: block;
    font-size: 3rem;
    fill: #ddd;
    width: 3rem;
    height: 3rem;
    top: .6rem;
    right: .2rem;
    position: absolute;
    color: #999;
}
.list_item_footer p{
    list-style-type: disc;
    font-size: .4rem;
    color: #999;
    margin-left: .4rem;
